import { useNavigate } from "react-router-dom";
import { useState, useEffect, useRef } from "react";
import axios from "axios";
import LoginCentre from "./LoginCentre";
import { toast, ToastContainer } from "react-toastify";

const DashboardFollowing = ({ service }) => {
  const navigate = useNavigate();
  const [services, setServices] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [selectedService, setSelectedService] = useState(null);
  const modalRef = useRef();
  const [name, setName] = useState("");

  const fetchSearchedServices = async (query) => {
    try {
      const centreId = localStorage.getItem("centreId");
      if (!centreId) return;

      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centreService/searchService?query=${query}&centreId=${centreId}`
      );
      setServices(res.data.data);
    } catch (error) {
      console.error("Error fetching users:", error);
      setServices([]);
    }
  };

  const fetchServices = async () => {
    const centreId = localStorage.getItem("centreId");
    if (!centreId) return;

    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centreService/by-centre/${centreId}`
      );
      setServices(response.data.services || []);
      console.log("services", response.data);
    } catch (error) {
      console.error("Error fetching doctors:", error);
    }
  };

  useEffect(() => {
    const delayDebounce = setTimeout(() => {
      if (name.trim() !== "") {
        fetchSearchedServices(name);
      } else {
        fetchServices();
      }
    }, 500);

    return () => clearTimeout(delayDebounce);
  }, [name]);

  useEffect(() => {
    fetchServices();
  }, []);

  // Handle click outside the modal to close it
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (modalRef.current && !modalRef.current.contains(event.target)) {
        setShowModal(false);
      }
    };

    if (showModal) {
      document.addEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [showModal]);

  const handleDelete = async (ServiceId) => {
    const confirmDelete = window.confirm(
      "Are you sure you want to delete this service?"
    );
    if (!confirmDelete) return;

    try {
      await axios.delete(
        `${process.env.REACT_APP_BACKEND_URL}/api/centreService/${ServiceId}`
      );
      toast.success("Service deleted successfully!");

      setServices((prev) =>
        prev.filter((service) => service._id !== ServiceId)
      );
    } catch (error) {
      console.error("Failed to delete service:", error);
      toast.error("Failed to delete service. Please try again.");
    }
  };

  const handleViewDetails = (service) => {
    setSelectedService(service);
    setShowModal(true);
  };

  const handleEdit = (id) => {
    navigate(`/edit-service/${id}`);
  };

  // Pagination Logic
  const [currentPage, setCurrentPage] = useState(1);
  const servicesPerPage = 7;
  const indexOfLastService = currentPage * servicesPerPage;
  const indexOfFirstService = indexOfLastService - servicesPerPage;
  const currentServices = services.slice(
    indexOfFirstService,
    indexOfLastService
  );
  const totalPages = Math.ceil(services.length / servicesPerPage);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

    const handlePageChangeWithScroll = (page) => {
  handlePageChange(page); // your existing page change logic
  window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
};


  const renderPagination = () => {
    const pages = [];
    const maxVisible = 4;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalPages) {
      endPage = totalPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

     pages.push(
    <li
      key="first"
      className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
       
      >
        First
      </button>
    </li>
  );

    // Prev Button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)}
        >
          Prev
        </button>
      </li>
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button className="page-link" onClick={() => handlePageChangeWithScroll(i)}>
            {i}
          </button>
        </li>
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalPages && handlePageChangeWithScroll(currentPage + 1)
          }
        >
          Next
        </button>
      </li>
    );

      pages.push(
      <li
        key="last"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalPages)}
        >
          Last
        </button>
      </li>
    );

    return pages;
  };

  useEffect(() => {
    // Handle click outside
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showModal
      ) {
        setShowModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // Scroll lock logic
    if (showModal) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto";
    };
  }, [showModal]);

  return (
    <div className="dashboard-body__content">
      {/* Service List */}
      <div className="card common-card border border-gray-five">
        <div className="card-body">
          {/* Header Section with responsive classes */}
          <div className="services-header">
            <h4 className="services-title">Available Services</h4>
            <div className="services-actions">
              <input
                type="text"
                className="common-input border-gray-five services-search-input"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="Search by service category or name"
              />
              <button
                className="create-service-btn"
                onClick={() => navigate("/create-service")}
              >
                Create Services
              </button>
            </div>
          </div>

          {currentServices.length > 0 ? (
            <div className="table-responsive">
              <table className="table text-body">
                <thead>
                  <tr>
                    <th>SN.</th>
                    <th>Category</th>
                    <th>Service Name</th>
                    <th>Actual Price</th>
                    <th>Platform Fee</th>
                    <th>Doctors</th>
                    <th>Time Slots</th>
                    <th>Description</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {currentServices.map((service, index) => (
                    <tr key={service._id}>
                      <td>{indexOfFirstService + index + 1}</td>
                      <td>{service.serviceCategory}</td>
                      <td>{service.serviceName}</td>
                      <td style={{ textAlign: "left" }}>
                        
                        {service.discountPrice
                          ? service.discountPrice
                          : service.actualPrice}
                      </td>
                      <td style={{ textAlign: "left" }}>
                        {" "}
                        {service.platformFees}
                      </td>

                      <td style={{ textAlign: "left" }}>
                        {service?.doctors.slice(0, 3).map((doc, i) => (
                          <div key={i}>
                            {doc.name}
                            {i === 2 && service.doctors.length > 3 && (
                              <span
                                style={{
                                  fontSize: "12px",
                                  color: "blue",
                                  cursor: "pointer",
                                  marginLeft: "8px",
                                  textDecoration: "underline",
                                }}
                                onClick={() => handleViewDetails(service)}
                              >
                                View More
                              </span>
                            )}
                          </div>
                        ))}
                      </td>
                      <td style={{ textAlign: "left" }}>
                        {service.slots.slice(0, 3).map((slot, i) => (
                          <div key={i}>
                            {slot.day.slice(0, 3)}: {slot.from} - {slot.to}
                          </div>
                        ))}
                        {service.slots.length > 3 && (
                          <span
                            style={{
                              fontSize: "12px",
                              color: "blue",
                              cursor: "pointer",
                              marginLeft: "8px",
                              textDecoration: "underline",
                            }}
                            onClick={() => handleViewDetails(service)}
                          >
                            View More
                          </span>
                        )}
                      </td>

                      <td
                        style={{
                          maxWidth: "300px",
                          overflow: "hidden",
                          whiteSpace: "nowrap",
                          textOverflow: "ellipsis",
                        }}
                      >
                        {service.description}
                      </td>
                      <td>
                        <button
                          onClick={() => {
                            setSelectedService(service);
                            setShowModal(true);
                          }}
                          style={{
                            backgroundColor: "#007bff",
                            color: "white",
                            border: "none",
                            borderRadius: "4px",
                            padding: "5px 10px",
                            marginRight: "5px",
                          }}
                        >
                          View
                        </button>
                        <button
                          style={{
                            backgroundColor: "green",
                            color: "white",
                            border: "none",
                            borderRadius: "4px",
                            padding: "5px 10px",
                            marginRight: "5px",
                          }}
                          onClick={() => handleEdit(service._id)}
                        >
                          Edit
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <p className="text-muted">No services added yet.</p>
          )}

          {totalPages > 1 && (
            <div className="flx-between gap-2">
              <div className="paginate-content flx-align flex-nowrap gap-3">
                <span className="paginate-content__text fs-14">
                  Showing {indexOfFirstService + 1} {" "}
                  {Math.min(indexOfLastService, services.length)} of{" "}
                  {services.length}
                </span>
              </div>
              <nav aria-label="Doctor pagination" className="mt-3">
                <ul className="pagination justify-content-center">
                  {renderPagination()}
                </ul>
              </nav>
            </div>
          )}
        </div>
      </div>

      {/* Modal code remains the same */}
      {showModal && selectedService && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
          onClick={() => setShowModal(false)}
        >
          <div
            ref={modalRef}
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "700px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              position: "relative",
              overflow: "hidden",
              maxHeight: "90vh",
              display: "flex",
              flexDirection: "column",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            {/* Modal content remains the same */}
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                position: "sticky",
                top: 0,
                zIndex: 10,
              }}
            >
              {selectedService.serviceName}
              <button
                onClick={() => setShowModal(false)}
                style={{
                  position: "absolute",
                  top: "10px",
                  right: "10px",
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            <div
              style={{
                padding: "20px",
                overflowY: "auto",
                flex: 1,
              }}
            >
              <div style={{ display: "flex", flexWrap: "wrap", gap: "20px" }}>
                <div style={{ flex: "1", minWidth: "250px" }}>
                  <div style={{ marginBottom: "20px" }}>
                    <p><strong style={{ color: "black" }}>Category</strong></p>
                    <p>{selectedService.serviceCategory}</p>
                  </div>
                  <div style={{ marginBottom: "20px" }}>
                    <p><strong style={{ color: "black" }}>Price</strong></p>
                    <p>{selectedService.totalPrice}</p>
                  </div>
                  <div style={{ marginBottom: "20px" }}>
                    <p><strong style={{ color: "black" }}>Doctor Report Uploading Time</strong></p>
                    <p>{selectedService.reportTiming}</p>
                  </div>
                </div>

                <div style={{ flex: "1", minWidth: "250px" }}>
                  <div style={{ marginBottom: "20px" }}>
                    <p><strong style={{ color: "black" }}>Doctors</strong></p>
                    <ul>
                      {selectedService.doctors.map((doc, i) => (
                        <li key={i}>{doc?.name}</li>
                      ))}
                    </ul>
                  </div>
                  <div style={{ marginBottom: "20px" }}>
                    <p><strong style={{ color: "black" }}>Time Slots</strong></p>
                    <ul>
                      {selectedService.slots.map((slot, i) => (
                        <li key={i}>
                          {slot.day}: {slot.from} - {slot.to}
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
              </div>

              <div style={{ marginTop: "20px" }}>
                <p><strong style={{ color: "black" }}>Description</strong></p>
                <p>{selectedService.description}</p>
              </div>
            </div>
          </div>
        </div>
      )}

      <ToastContainer position="top-right" autoClose={3000} />

      {/* Responsive CSS */}
      <style>
        {`
           .services-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
    }

    .services-actions {
      display: flex;
      gap: 15px;
      align-items: center;
      flex: 1; /*  Makes actions take available space */
      justify-content: flex-end; /*  Aligns items to the right */
    }

    .services-search-input {
      flex: 1; /*  Makes search input expand to fill space */
      max-width: 300px; /*  Increased maximum width */
      min-width: 250px; /*  Minimum width */
    }

    .create-service-btn {
      background-color: green;
      color: whitesmoke;
      padding: 10px 16px;
      border-radius: 5px;
      font-weight: 600;
      border: none;
      cursor: pointer;
      white-space: nowrap;
      flex-shrink: 0; /*  Prevents button from shrinking */
    }

    /* Mobile Responsive Styles */
    @media (max-width: 768px) {
      .services-header {
        flex-direction: column;
        align-items: start;
        gap: 15px;
      }

      .services-title {
        text-align: center;
        margin-bottom: 0;
      }

      .services-actions {
        flex-direction: column;
        gap: 10px;
        width: 100%;
        justify-content: stretch; /*  Full width on mobile */
      }

      .services-search-input {
        max-width: 100% !important;
        width: 100%;
        flex: none; /*  Reset flex on mobile */
      }

      .create-service-btn {
        width: 100%;
        padding: 12px;
      }
    }

          /* Small mobile devices */
          @media (max-width: 480px) {
            .services-header {
              gap: 10px;
            }
            
            .table-responsive {
              font-size: 12px;
            }
          }
        `}
      </style>
    </div>
  );
};

export default DashboardFollowing;